.oxir {
    width: 100%;
    background-color: #0EB4C1;

}
.oxir .card___wrapper3{
    width: 1100px;
    margin: auto;
}
.oxir .card___wrapper3 .first{
    display: block;
    text-decoration: none;
    text-align: center;
    font-size: 36px;
    font-weight: 700;
    padding: 40px 0px;
}
.oxir .card___wrapper3 .but{
    border-radius: 5px;
    padding: 7px;
    font-size: 15px;
    font-weight: 400;
    background-color: #0EB4C1;
    color: white;
    border: 1px solid white;
}
.oxir .card___wrapper3 .but:hover{
    background-color: #DE2E54;
    color: white;
    border: 1px solid #DE2E54;
}
.oxir .card___wrapper3 .but:active{
    opacity: 60%;
}
.ortiqcha{
    width: 1100px;
    margin: auto;
    height: 50px;
    background-color: #0EB4C1;
}