.asos {
    width: 100%;
    background-color: #0EB4C1
}

.asos .card___wrapper {
    width: 1100px;
    margin: auto;
}

.asos .card___wrapper .card {
    display: inline-block;
    width: 30%;
    color: white;
    vertical-align: middle;
    margin: 20px 50px 0px 100px;
}

.asos .card___wrapper .card:nth-child(1) {
    width: 40%;

}

.asos .card___wrapper .card img {
    width: 100%;
}

.asos .card___wrapper .card h1 {
    font-size: 36px;
    font-weight: 700;
}

.asos .card___wrapper .card p {
    font-size: 18px;
    font-weight: 400;
    margin: 10px 0px;
}

.btn {
    background-color: #DE2E54;
    color: white;
    border: 2px solid #DE2E54;
    width: 150px;
    height: 39px;
    padding: 12px 16px;
    border-radius: 7px;
    margin-left: 40%;
}

.btn:hover{
    background-color: white;
    color: #DE2E54;
}
.btn:active{
    opacity: 60%;
    background-color: #DE2E54;
    color: white;
    border: 2px solid #DE2E54;
}

.btn2:hover{
    opacity: 75%;
    background-color: #DE2E54;
    color: white;
    border: 2px solid #DE2E54;
}
.btn2:active{
    opacity: 60%;
}