.urta {
    width: 100%;
    background-color: rgb(245, 244, 244);

}

.urta .card___wrapper2 {
    width: 1100px;
    margin: auto;
    padding: 80px;
}

.urta .card___wrapper2 .h1 {
    font-size: 36px;
    font-weight: 700;
    text-align: center;
}
.urta .card___wrapper2 .card1{
    width: 90%;
    margin: 20px 50px;
    text-align: center;
}
.urta .card___wrapper2 .card1 .rasm{
    display: inline-block;
    vertical-align: middle;
}
.urta .card___wrapper2 .card1 .rasm img{
    width: 160px;
}
.urta .card___wrapper2 .card1 .matn{
    display: inline-block;
    vertical-align: middle;
    width: 55%;
    margin: 0px 20px;
    text-align: left;
    vertical-align: middle;
}