footer {
    width: 100%;
    margin: auto;
    background-color: hsl(0, 0%, 13%);

}
footer nav {
    width: 1100px;
    margin: auto;
}

footer nav ul {
    display: inline-block;
    margin: 10px 0px;

}

footer nav ul:nth-child(2) {
    float: right;
    margin-top: 25px;
}

footer nav ul li {
    display: inline-block;
    margin: auto;
}

footer nav ul li a {
    text-decoration: none;
    padding: 16px 16px;
    margin: 0px 20px 20px;
    font-size: 14px;
    font-weight: 700;
    color: white;
    
}
footer nav ul li a:hover{
    color: #DE2E54;
}
footer nav ul li a:active{
    opacity: 60%;
}