header {
    width: 100%;
    margin: auto;
}

header nav {
    width: 1100px;
    margin: auto;
    display: flex;
    justify-content: space-between;
}

header nav ul {
    display: flex;
    margin: 10px 0px;
    justify-content: center;
    flex-wrap: wrap;
    gap: 1rem;

}
header nav ul li {
    display: inline-block;
    margin: auto;
}

header nav ul li a {
    text-decoration: none;
    padding: 8px 16px;
    margin: 0px 20px 20px;
    font-size: 14px;
    font-weight: 700;
    color: black;
}
header nav ul li a:hover{
    color: #DE2E54;
}
header nav ul li a:active{
    opacity: 60%;
}
header nav ul button{
    background-color: #DE2E54;
    color: white;
    border: 2px solid #DE2E54;
    width: 250px;
    height: 39px;
    /* padding: 12px 16px; */
    border-radius: 7px;
    margin-left: 40%;
}
.btn3:hover{
    background-color: white;
    color: #DE2E54;
}
.btn3:active{
    opacity: 60%;
    background-color: #DE2E54;
    color: white;
    border: 2px solid #DE2E54;
}